* {
    box-sizing: border-box;
    font-family: 'Open Sans', sans-serif;
    margin: 0;
    padding: 0;
}

html, body {
    margin: 0;
    padding: 0;
    background-color: #161616;
    font-family: "Inter", sans-serif;
}

body::-webkit-scrollbar {
  display: none;
}

body {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.site-wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
}

header {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #FFF7ED;
    padding-left: calc(50% - max(5%, 400px));
    padding-right: calc(50% - max(5%, 400px));
    min-height: 50px;
}

header .site-logo {
    color: black;
    margin: auto;
    margin-left: 10px;
    text-transform: uppercase;
    font-weight: 900;
    font-size: 25px;
}

header a {
    text-decoration: none;
    color: #4D4D4D;
    font-weight: 600;
    padding: 5px 20px;
}

header nav {
    width: 100%;
    display: flex;
    justify-content: right;
    align-items: center;
}

header nav a:hover, .active-nav {
    color: #161616;
    text-decoration: underline;
}

.header--dir {
    width: 100%;
    height: 24px;
    background-color: #2a2a2a;
    position: relative;
    display: flex;
    align-items: center;
    gap: 10px;
    padding-left: calc(max(10px, 50% - max(5%, 400px)));
    padding-right: calc(max(10px, 50% - max(5%, 400px)));
}

.header--dir-link {
    color: #F0D07F;
    text-decoration: none;
    font-weight: 500;
}

.header--dir-link:not(:first-child) {
    margin-left: 5px;
}

.header--dir-link:hover {
    text-decoration: none;
    font-weight: 600;
}

.header--dir-link:not(:first-child)::before {
    content: "/";
    color: #FFFFFF;
    position: absolute;
    translate: -11px;
    width: 20px;
    height: 20px;
    font-weight: 400;
}

footer {
    background-color: #161616;
    color: #AAAAAA;
    height: 74px;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    margin-top: auto;
    font-weight: 500;
}

.link-button {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    border: none;
    padding: .75rem 1.375rem;
    color: initial;
    font-weight: 700;
    border-radius: 5px;
    cursor: pointer;
    transition-property: all;
    transition: .1s ease-in-out;
}

.link-button:hover {
    background-color: purple;
}

.search--suggestions {
    position: absolute;
    display: none;
    padding: 0 5px 0 5px;
    max-height: 200px;
    min-height: 0;
    width: 200px;
    background-color: white;
    overflow-y: scroll;
}

.search--suggestion {
    overflow: hidden;
    text-overflow: ellipsis;
    width: 80%;
}

.api--header:focus-within .search--suggestions,
.search--suggestions:hover {
    display: block;
}


.home--container {
    background: linear-gradient(0deg, rgba(0, 0, 0, 0.46), rgba(0, 0, 0, 0.46)), url("./assets/images/home-hero.png") no-repeat center center;
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover;
    background-attachment: fixed;
    min-height: 80vh;
    color: white;
    padding-top: 45px;
    padding-bottom: 20px;
    padding-left: calc(48% - max(10%, 300px));
    padding-right: calc(48% - max(10%, 300px));
    display: flex;
    flex-direction: column;
}

.home--content {
    background-color: rgba(0, 0, 0, 0.5);
    border-radius: 10px;
}

.home--content > * {
    padding: 10px 10px 17px 10px;
}

.home--header {
    font-weight: 700;
    font-size: 2.25rem;
    line-height: 42px;
    text-shadow: black 0 0 6px;
    padding: 10px 10px 17px 10px;
}

.home--paragraph {
    line-height: 24px;
    font-size: 1.1rem;
}

.home--list {
    padding-left: 40px;
    padding-right: 30px;
}

.home--list > li {
    margin-bottom: 10px;
    overflow-wrap: break-word;
}

.site--link1 {
    display: inline-block;
    text-align: center;
    text-decoration: none;
    background-color: #FF8C38;
    border: none;
    width: 100%;
    margin-top: 27px;
    padding-block: .75rem;
    color: white;
    font-weight: 700;
    border-radius: 5px 30px 5px 10px;
    cursor: pointer;
    transition:
        transform .1s ease-in-out,
        background-color .1s ease;
    max-width: 400px;
    align-self: center;
    text-shadow: none;
}

.home--api-link:hover {
    transform: translate(1px, 1px);
    background-color: #FF8C20;
}

.about--container {
    /*padding: 0 min(30%, 30vh);*/
}

.about--hero {
    width: 100%;
    min-height: 50px;
}

.about--content {
    padding-inline: 23px;
    color: #161616;
    padding: 45px min(10%, 300px);
}

.about--content h1 {
    line-height: 38px;
}

.about--content p {
    line-height: 22px;
}

.about--cta {
    background-color: #FFCC8D;
    color: #161616;
    padding-inline: 32px;
    padding-bottom: 32px;
    margin-inline: 27px;
    border-radius: 5px;
    margin-top: 55px;
}

.about--cta h2 {
    margin: 0;
    padding-block: 37px;
}

.about--cta .link-button {
    background-color: #161616;
    color: white;
    border-radius: 10px;
}

.notfound--container {
    padding-inline: 26px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 50vh;
}

.notfound--text {
    text-align: center;
}

.notfound--image {
    height: 150px;
    width: 75px;
    margin-top: 10px;
}

.footer--logo {
    height: 50px;
    margin-left: 10px;
}

.footer--gap {
    margin-right: 10px;
}

.api--tags {
    width: 100%;
    max-width: 100%;

    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
    gap: 5px;
}

.api--tag {
    padding: 2px 5px 2px 5px;
    background-color: #2a2a2a;
    color: #f0d080;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
    height: 25px;
    width: fit-content;
    max-width: 150px;
    display: flex;
    justify-content: space-between;
}

.api--tag-count {
    margin-left: 5px;
    padding: 0 2px 0 2px;
    height: 100%;
    background-color: #3D3D3D;
    border-radius: 4px;
}

.api--tag-header {
    height: 25px;
}

.func--background {
    margin: 0;
    padding: 10px;
    background-color: #161616;
}

.func--category-button {
    margin: 0 0 0 20px;
    background-color: rgba(255, 255, 255, 0);
    border: none;
}

.func--category-button:active > .func--category-header {
    color: #f0d080;
}

.func--category-header {
    margin: 0;
    color: #ffffff;
    font-size: 40px;
    font-weight: bolder;
    cursor: pointer;
}

.func--category-description {
    margin: 0 10px 0 10px;
    white-space: pre-wrap;
    overflow-y: hidden;
    line-height: 20px;
}

.func--grid {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    padding: 10px 10px;
    gap: 10px;
}

.func--flex {
    display: flex;
    margin: 10px;
    gap: 5px;
    flex-wrap: wrap;
    align-items: flex-end;
}

.func {
    padding: 10px;
    border-radius: 5px;
    border: solid 1px #4D4D4D;
    display: grid;
    grid-template-columns: 50px 1fr 1fr 50px;
    grid-template-rows: repeat(7, auto) 1fr auto;
    background-color: #161616;
}

.func:hover {
    border: solid 1px #aaaaaa;
}

.func--header {
    grid-area: header;
    margin-top: 0;
    margin-bottom: 0;
    display: flex;
    align-items: center;
    gap: 10px;
}

.func--header > a, .func--header-nolink {
    color: #F0D07F;
    font-size: 26px;
    font-weight: bolder;
    text-decoration: none;
}

.func--header > a:hover, .func--header > a:focus {
    color: #EEC96D;
    text-decoration: underline;
}

.func--header > a:active {
    color: #F2D791;
    text-decoration: none;
}

.func--back {
    grid-area: back;
    padding-right: auto;
    padding-bottom: auto;
    display: flex;
}

.func--back-image {
    height: 50px;
    transition: transform 0.4s;
    align-self: center;
}

.func--back-image:hover, .func--back-image:focus {
    transform: scale(1.1);
}

.func--back-image:active {
    transform: scale(1);
}

.func--text {
    margin-top: 10px;
    margin-bottom: 0;
    color:#AAAAAA;
}

.func--func {
    color:#ffffff;
}

.func--tags {
    grid-area: tags;
    margin: 10px 0 0 0;
}

.func--example {
    grid-area: example;
}

.func--returns {
    grid-area: returns;
    margin-right: 10px;
}

.func--arguments {
    grid-area: arguments;
}

.func--description {
    grid-area: description;
    flex-grow: 1;
    white-space: pre-wrap;
}

.func--code {
    grid-area: code;
    margin-top: 10px;
    padding: 20px;
    width: 100%;
    background-color:#2a2a2a;
    white-space: pre;
    max-height: 250px;
    overflow-x: scroll;
}

.func--table {
    grid-area: table;
}

table, th, tr {
    border: 1px solid #4D4D4D;
    border-collapse: collapse;
}

td {
    padding: 2px 5px 2px 5px;
}

th {
    padding: 0 10px 0 5px;
    background-color: #4D4D4D;
    text-align: left;
}

.func--return {
    margin-top: 0;
    margin-bottom: 0;
    color: #c0c0f0;;
}

.func--argument {
    margin-top: 0;
    margin-bottom: 0;
    color: #c0f0c0;
}

.func--status {
    font-size: 16px;
    font-weight: normal;
}

.func--experimental {
    font-size: 16px;
    font-weight: 600;
    color: red;
    margin: 0;
    padding: 0;
}

.func--prev {
    padding: 2px 5px 2px 5px;
    background-color: #2a2a2a;
    color: #f0d080;
    font-weight: 500;
    border-radius: 5px;
    cursor: pointer;
}

.func--page {
    padding-top: 45px;
    padding-bottom: 20px;
    padding-left: calc(40% - max(10%, 300px));
    padding-right: calc(40% - max(10%, 300px));
}

.func--page-content {
    display: grid;
    grid-template-columns: 65% 35%;
    gap: 5px;
    overflow-wrap: break-word;
}

.courier {
    font-family: 'Courier New', Courier, monospace;
}

.red {
    color: red;
}

.white {
    color: #FFF7ED;
}

.bold {
    font-weight: bolder;
}

.api-link {
    color: #0066CC;
}

.api-link {
    text-decoration: none;
}

.api-link:visited {
    color: darkorchid;
}

.tag--count {
    margin-left: 5px;
    padding: 0 2px 0 2px;
    height: 5px;
    background-color: #3D3D3D;
    border-radius: 4px;
}

.api--header {
    position: sticky;
    top: 0;
    left: 0;
    width: 100%;
    background-color: #161616;
    border-bottom: #2a2a2a solid 1px;
    padding: 10px;
    z-index: 2;
    padding-left: calc(max(10px, 50% - max(5%, 400px)));
    padding-right: calc(max(10px, 50% - max(5%, 400px)));
}

.overlay--background {
    z-index: 3; 
    background-color: rgba(0, 0, 0, 0.9);
    top: 110px;
    left: 0;
    width: 100%;
    height: 100%;
    position: absolute;
    padding: 100px;

    @media screen and (max-width: 768px) {
        padding: min(1%, 20px);
    }
}